import React, { useEffect, useContext } from "react";
import { useSnackbar } from 'notistack';
import { socket } from '../context/SocketContext';
import { ServerContext } from '../context/ServerContext';
import { BalancesContext } from '../context/BalancesContext';
import { PerformanceContext } from '../context/PerformanceContext';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import { StartingBalanceContext } from '../context/StartingBalanceContext';
import Button from '@material-ui/core/Button';
import pckg2 from '../version2.json';

export default function SocketService() {
    const version = pckg2.version;

    const [{ status, serverversion }, setServer] = useContext(ServerContext);
    const [balances, setBalances] = useContext(BalancesContext);
    const [performance, setPerformance] = useContext(PerformanceContext);
    const setConfiguration = useContext(ConfigurationContext)[1];
    const setConfigurationInput = useContext(ConfigurationInputContext)[1];
    const setStartingBalance = useContext(StartingBalanceContext)[1];

    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    const action = key => (
        <React.Fragment>
            <Button
                style={{
                    color: '#bbbbbb',
                }}
                onClick={() => { closeSnackbar(key) }}>
                Dismiss
            </Button>
        </React.Fragment>
    );

    const handleEvent = (log, summary) => {
        const variant = (log.type === 'order') ? 'info' : log.type;
        enqueueSnackbar(summary, {
            variant: variant,
            autoHideDuration: 8000,
            action,
        });
    };

    const updatePerformance = () => {
        const rows = (balances.portfolio) ? JSON.parse(balances.portfolio) : [];
        let update = [];

        if (performance.length !== 0) {
            const today = performance[performance.length - 1].coins;

            rows.forEach((coin) => {
                const match = today.find(c => c.coin === coin.coin);
                update.push({
                    ...match,
                    number: coin.number,
                    price: coin.last,
                });
            });

            let newPerformance = [...performance];
            newPerformance[newPerformance.length - 1].coins = update;
            newPerformance[newPerformance.length - 1].eur = balances.eur;
            newPerformance[newPerformance.length - 1].usd = balances.usd;
            setPerformance(newPerformance);
        }
    }

    useEffect(() => {
        // Reload page on server startup if new BB version has been installed, to make sure server and client are in sync
        if (status === 'starting' && serverversion !== '0.0' && version !== serverversion) window.location.reload();
        else if (status === 'starting')
            setServer(prevState => {
                return {
                    ...prevState,
                    status: 'connected'
                }
            });

        return () => {
        }
    }, [status, serverversion]);

    useEffect(() => {
        updatePerformance();

        return () => {
        }
    }, [balances]);

    useEffect(() => {
        socket.on("status", (server) => {
            setServer(prevState => {
                return {
                    ...prevState,
                    ...server
                }
            });
        });
        socket.on("balances", (data) => {
            setBalances(data.balances);
            setServer(prevState => {
                return {
                    ...prevState,
                    status: 'connected',
                    updated: data.balances.time,
                }
            });
        });
        socket.on("performance", (logs) => {
            setPerformance(logs);
        });
        socket.on("configuration", (config) => {
            setConfiguration(config);
            setConfigurationInput({ ...config, recreate: false });
        });
        socket.on("starting-balances", (sb) => {
            setStartingBalance(sb);
        });
        socket.on("event", function (data) {
            handleEvent(data.log, data.summary);
        });
        socket.on("disconnect", data => {
            setBalances({});
            setServer(prevState => {
                return {
                    ...prevState,
                    status: 'disconnected',
                }
            });
        });

        return () => {
            socket.disconnect();
        }
    }, []);

    return (null);
}