import React, { useState, useContext, useEffect, useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import { DataGrid } from '@material-ui/data-grid';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import { format, parse, subDays, isBefore } from 'date-fns';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    DatePicker,
} from '@material-ui/pickers';
import Link from '@material-ui/core/Link';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import { PerformanceContext } from '../context/PerformanceContext';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import { HtmlTooltip } from './HtmlToolTip';
import IconButton from '@material-ui/core/IconButton';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import cloneDeep from 'lodash.clonedeep';

const columns = [
    { field: 'coin', headerName: 'Coin', width: 350, editable: false },
    { field: 'number', headerName: 'Number', type: 'number', width: 200, editable: true, valueFormatter: ({ value }) => (Number(value)) },
    { field: 'price', headerName: 'Price', type: 'number', width: 200, editable: true, valueFormatter: ({ value }) => (Number(value)) },
    { field: 'suggested', headerName: 'Suggested Change', type: 'string', width: 200, editable: false, valueFormatter: ({ value }) => (value ? '✔' : '') },
];

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    header: {
        "& .MuiDataGrid-colCell":
            theme.palette.type === 'light'
                ? {
                    backgroundColor: '#aaaaaa',
                }
                : {
                    backgroundColor: '#777777',
                },
    },
    paper: {
        width: '100%',
        marginBottom: theme.spacing(2),
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
    day: {
        width: 36,
        height: 36,
        // fontSize: theme.typography.caption.fontSize,
        fontSize: "inherit",
        margin: "0",
        textAlign: "center",
        color: "inherit",
    },
    highlight: {
        background: theme.palette.secondary.main,
        color: theme.palette.common.white,
        borderRadius: "50%",
    },
    highlightSelected: {
        background: theme.palette.primary.main,
        color: theme.palette.common.white,
        borderRadius: "50%",
    },
    today: {
        background: '#777',
        color: theme.palette.common.white,
        borderRadius: "50%",
    },
    outofbounce: {
        color: theme.palette.text.disabled,
        borderRadius: "50%",
    },
}));

export default function ConfigP3(props) {
    const classes = useStyles();

    const now = format(new Date(), 'yyyy/MM/dd');

    const input = useContext(ConfigurationInputContext)[0];
    const [pickerOpen, setPickerOpen] = useState(false);
    const configuration = useContext(ConfigurationContext)[0];
    const performance = useContext(PerformanceContext)[0];
    const initRef = useRef(false);
    const liquidityRef = useRef(cloneDeep(input.liquidity));
    const liqIndexRef = useRef(-1);
    const selectedDateRef = useRef(now);
    const [balanceDay, setBalanceDay] = useState([]);
    // const [recreate, setRecreate] = useState(input.recreate);

    const checkLiquidityChanges = () => {
        const inp = input.portfolio;
        const excludedChange = input.excludedChange;
        const today = format(new Date(), 'yyyy/MM/dd');

        let changes = [];

        inp.forEach(coin => {
            const match = excludedChange ? excludedChange.find(c => c.coin === coin.coin) : false;
            // If coin's excluded state was changed (once or more) in step 2
            if (match) {
                const original = configuration.portfolio.find(c => c.coin === coin.coin);
                // Get the original liquidity when toggling excluded
                const liq = configuration.liquidity.find(day => day.date === today);
                const liqcoin = liq ? liq.changes.find(c => c.coin === coin.coin) : false;
                const current = liq ? (liqcoin ? liqcoin : { number: 0 }) : { number: 0 };

                if (match.excluded !== original.excluded) {
                    if (coin.excluded) {
                        changes.push({
                            coin: coin.coin,
                            number: current.number - coin.number,   // In case a coin is excluded during day 1
                            price: (current.number - coin.number !== 0) ? coin.price : 0,
                            suggested: true,
                        });
                    }
                    else {
                        changes.push({
                            coin: coin.coin,
                            number: current.number + coin.number,   // In case a coin is included that was earlier excluded on the same day
                            price: (current.number + coin.number !== 0) ? coin.price : 0,
                            suggested: true,
                        });
                    }
                } else {
                    // Reset to original liquidity (e.g. when toggled twice)
                    changes.push({
                        coin: coin.coin,
                        number: current.number,
                        price: (current.number !== 0) ? current.price : 0,
                        suggested: false,
                    });
                }
            } else {
                if (liqIndexRef.current !== -1) {
                    const liq = liquidityRef.current[liqIndexRef.current];
                    const match = liq.changes.find(c => c.coin === coin.coin);
                    if (match) {
                        changes.push({
                            coin: match.coin,
                            number: match.number,
                            price: match.price,
                        });
                    }
                }
            }
        });

        if (changes.length !== 0) {
            const today = format(new Date(), 'yyyy/MM/dd');

            if (liqIndexRef.current !== -1) {
                liquidityRef.current[liqIndexRef.current] = {
                    date: today,
                    changes: changes,
                }
            } else {
                liquidityRef.current.push({
                    date: today,
                    changes: changes,
                });
            }

            liqIndexRef.current = liquidityRef.current.length - 1;

            props.callback({
                field: 'liquidity',
                input: liquidityRef.current,
            });
        }
    }

    useEffect(() => {
        initRef.current = (input.apiKey !== '************' && input.apiSecret !== '************');

        const today = format(new Date(), 'yyyy/MM/dd');
        selectedDateRef.current = today;

        const liq = liquidityRef.current.find(day => day.date === today);

        if (liq) {
            liqIndexRef.current = liquidityRef.current.findIndex(day => day.date === today);
        } else {
            liqIndexRef.current = -1;
        }

        if (!initRef.current) checkLiquidityChanges();

        acceptDateChange(new Date());

        // set liquidity defaults
        if (initRef.current) {
            handleCurrent();
        }

        return () => {
        }
    }, []);

    const updateBalanceDay = (changes) => {
        changes.sort((a, b) => (a.coin > b.coin) ? 1 : ((b.coin > a.coin) ? -1 : 0));

        setBalanceDay(changes);
    }

    const handleDateChange = (indate) => {
    };

    const acceptDateChange = (indate) => {
        setPickerOpen(false);

        const date = format(indate, "yyyy/MM/dd");
        if (isBefore(parse(date, 'yyyy/MM/dd', new Date()), parse(input.startdate, 'yyyy/MM/dd', new Date()))) {
            return;
        }

        selectedDateRef.current = date;

        if (!initRef.current) {
            let coins = [];

            // Select coins to be shown in the balances table
            // Only the ones that are in the performance file for the selected date
            let perf = performance.find(day => day.date === date);
            if (perf) {
                perf.coins.forEach(coin => {
                    coins.push({
                        id: coin.coin,
                        coin: coin.coin,
                        number: 0,
                        price: 0,
                    });
                });
            }

            // Include coins from the previous day, in case on the selected day a coin was excluded / removed
            const today = parse(date, 'yyyy/MM/dd', new Date());
            const yesterday = format(subDays(today, 1), 'yyyy/MM/dd');
            perf = performance.find(day => day.date === yesterday);
            if (perf) {
                perf.coins.forEach(coin => {
                    if (!coins.find(c => c.coin === coin.coin)) {
                        coins.push({
                            id: coin.coin,
                            coin: coin.coin,
                            number: 0,
                            price: 0,
                        });
                    }
                });
            }

            // Include coins that are in portfolio if selected date is today
            if (selectedDateRef.current === now) {
                input.portfolio.forEach(coin => {
                    if (!coin.excluded && !coins.find(c => c.coin === coin.coin)) {
                        coins.push({
                            id: coin.coin,
                            coin: coin.coin,
                            number: 0,
                            price: 0,
                        });
                    }
                });
            }

            const liq = liquidityRef.current.find(day => day.date === date);

            if (liq) {
                // Include coins that are stored in liquidity (could be a coin that got included in step 2)
                liq.changes.forEach(coin => {
                    if (!coins.find(c => c.coin === coin.coin)) {
                        coins.push({
                            id: coin.coin,
                            coin: coin.coin,
                            number: 0,
                            price: 0,
                        });
                    }
                });

                liqIndexRef.current = liquidityRef.current.findIndex(day => day.date === date);
                let newChanges = [];
                coins.forEach(coin => {
                    let match = liq.changes.find(c => c.coin === coin.coin);
                    if (match) {
                        newChanges.push({
                            id: coin.coin,
                            coin: coin.coin,
                            number: match.number,
                            price: match.price,
                            suggested: match.suggested,
                        });
                    } else {
                        newChanges.push({ ...coin });
                    }
                });
                coins = [...newChanges];
            } else {
                liqIndexRef.current = -1;
            }
            updateBalanceDay(coins);
        }
    };

    // const handleCheck = (e) => {
    //     const checked = e.target.checked;
    //     props.callback({
    //         field: 'recreate',
    //         input: checked,
    //     });
    //     setRecreate(checked);
    // }

    const handleEditCellChangeCommitted = (e) => {
        // let newLiquidity = [...liquidityRef.current]; --> this doesn't work, as configuration somehow gets updated even when changes are not saved...
        let newLiquidity = cloneDeep(liquidityRef.current);

        let newIndex = liqIndexRef.current;

        if (newIndex === -1) {
            newIndex = newLiquidity.push({
                date: selectedDateRef.current,
                changes: [],
            }) - 1;

            liqIndexRef.current = newIndex;
        }

        if (newLiquidity[newIndex].changes) {
            let match = newLiquidity[newIndex].changes.find(c => c.coin === e.id);
            if (!match) {
                newLiquidity[newIndex].changes.push({ coin: e.id });
                match = newLiquidity[newIndex].changes.find(c => c.coin === e.id);
            }
            if (match) {
                match['id'] = e.id;
                match[e.field] = Number(e.props.value);
                // If number was entered, auto-fill the price field with current day price
                // If not available, take the previous day's price
                if (e.field === 'number') {
                    if (e.props.value !== '0') {
                        let perf = performance.find(day => day.date === selectedDateRef.current);
                        if (perf) {
                            let coin = perf.coins.find(c => c.coin === e.id);
                            if (coin) {
                                match['price'] = Number(coin.price);
                            } else {
                                const today = parse(selectedDateRef.current, 'yyyy/MM/dd', new Date());
                                const yesterday = format(subDays(today, 1), 'yyyy/MM/dd');
                                perf = performance.find(day => day.date === yesterday);
                                if (perf) {
                                    let coin = perf.coins.find(c => c.coin === e.id);
                                    if (coin) {
                                        match['price'] = Number(coin.price);
                                    }
                                }
                            }
                        }
                    } else {
                        match['price'] = 0;
                    }
                }
                let newBalance = [...balanceDay];
                let index = newBalance.findIndex(c => c.coin === e.id);
                newBalance[index] = match;
                setBalanceDay(newBalance);
            }
        }

        liquidityRef.current = newLiquidity;

        props.callback({
            field: 'liquidity',
            input: newLiquidity,
        });
    }

    const handleCurrent = () => {
        let newStartPerformance = [];

        input.portfolio.forEach((coin) => {
            if (initRef.current) {
                // Set default values at initial configuration
                if (!coin.excluded) {
                    newStartPerformance.push({
                        id: coin.coin,
                        coin: coin.coin,
                        number: Number((coin.number).toFixed(8)),
                        price: Number((coin.price).toFixed(8)),
                        suggested: true,
                    });
                }
            } else {
                let coins = [];
                let perf = performance.find(day => day.date === selectedDateRef.current);
                if (perf) {
                    perf.coins.forEach(coin => {
                        coins.push({
                            coin: coin.coin,
                        });
                    });
                }

                let match = coins.find(c => c.coin === coin.coin);
                if (match) {
                    newStartPerformance.push({
                        id: coin.coin,
                        coin: coin.coin,
                        number: Number((coin.number).toFixed(8)),
                        price: Number((coin.price).toFixed(8)),
                    });
                }
            }
        });
        updateBalanceDay(newStartPerformance);

        let newLiquidity = [];

        if (initRef.current) {
            let changes = [];

            newStartPerformance.forEach(coin => {
                changes.push({
                    coin: coin.coin,
                    number: coin.number,
                    price: coin.price,
                });
            });

            const today = format(new Date(), 'yyyy/MM/dd');

            newLiquidity.push({
                date: today,
                changes: changes,
            });
        } else {
            // newLiquidity = [...liquidityRef.current]; --> this doesn't work, as configuration somehow gets updated even when changes are not saved...
            newLiquidity = cloneDeep(liquidityRef.current);
            newLiquidity[liqIndexRef.current].changes.forEach((row) => {
                let match = newStartPerformance.find(c => c.coin === row.coin);
                if (match) {
                    row.number = match.number;
                    row.price = match.price;
                }
            });
        }

        liquidityRef.current = newLiquidity;

        props.callback({
            field: 'liquidity',
            input: newLiquidity,
        });
    }

    const handleDelete = () => {
        if (liqIndexRef.current !== 0) {
            let newStartPerformance = [];
            balanceDay.forEach((coin) => {
                newStartPerformance.push({
                    id: coin.coin,
                    coin: coin.coin,
                    number: 0,
                    price: 0,
                });
            });
            updateBalanceDay(newStartPerformance);

            liquidityRef.current.splice(liqIndexRef.current, 1);
            liqIndexRef.current = -1;

            props.callback({
                field: 'liquidity',
                input: liquidityRef.current,
            });
        }
    }

    const renderDay = (day, selectedDay, dayInCurrentMonth, dayComponent) => {
        let selected = format(selectedDay, 'yyyy/MM/dd');
        let date = format(day, 'yyyy/MM/dd');
        let d = format(day, 'd');
        const today = format(new Date(), 'yyyy/MM/dd');

        let dates = [];
        liquidityRef.current.forEach((day) => {
            dates.push(day.date);
        });

        // if (isBefore(parse(date, 'yyyy/MM/dd', new Date()), parse(input.startdate, 'yyyy/MM/dd', new Date()))) {
        //     if (dayInCurrentMonth) {
        //         return (
        //             <div className={classes.outofbounce}>
        //                 <IconButton className={classes.day}>
        //                     <span> {d} </span>
        //                 </IconButton>
        //             </div>
        //         )
        //     } else {
        //         return dayComponent;
        //     }
        // }

        if (date === selectedDateRef.current && dayInCurrentMonth) {
            return (
                <div className={classes.highlightSelected}>
                    <IconButton className={classes.day}>
                        <span> {d} </span>
                    </IconButton>
                </div>
            )
        }

        if (dates.includes(date) && dayInCurrentMonth && selected !== date) {
            return (
                <div className={classes.highlight}>
                    <IconButton className={classes.day}>
                        <span> {d} </span>
                    </IconButton>
                </div>
            );
        }

        if (date === today) {
            return (
                <div className={classes.today}>
                    <IconButton className={classes.day}>
                        <span> {d} </span>
                    </IconButton>
                </div>
            )
        }

        return dayComponent;
    }

    // function CustomToolbar() {
    //     return (
    //         <Grid container direction="row" alignItems="flex-end" justify="space-between">
    //             <Grid item xs={9} >
    //                 <MuiPickersUtilsProvider utils={DateFnsUtils}>
    //                     <DatePicker
    //                         disableToolbar="true"
    //                         variant="inline"
    //                         format="yyyy/MM/dd"
    //                         margin="normal"
    //                         id="date-picker-inline"
    //                         label="Select date"
    //                         value={selectedDateRef.current}
    //                         disableFuture="true"
    //                         onChange={handleDateChange}
    //                         onAccept={acceptDateChange}
    //                         renderDay={renderDay}
    //                     />
    //                 </MuiPickersUtilsProvider>
    //             </Grid>
    //             <Grid item xs={3}>
    //                 <Box display="flex" justifyContent="flex-end">
    //                     <Button disabled={liqIndexRef.current === 0 || liqIndexRef.current === -1} variant="contained" color="primary" startIcon={<ArrowDownwardIcon />} onClick={handleDelete}>
    //                         Delete Liq Changes
    //                     </Button>
    //                 </Box>
    //             </Grid>
    //             <Grid item xs={3} >
    //                 <Button variant="contained" color="primary" startIcon={<ArrowDownwardIcon />} onClick={handleCurrent}>
    //                     Fetch balance
    //                 </Button>
    //             </Grid>
    //             <Grid container alignItems="center" justify="space-between">
    //                 <Grid item>
    //                     <FormControlLabel
    //                         control={<Checkbox color="secondary" name="rewrite" value="yes" checked={recreate} onChange={handleCheck} />}
    //                         label="Recreate performance history"
    //                     />
    //                 </Grid>
    //                 <Grid item>
    //                     <Button variant="contained" color="primary" startIcon={<ArrowDownwardIcon />} onClick={handleCurrent}>
    //                         Use current balance
    //                     </Button>
    //                 </Grid>
    //             </Grid>
    //         </Grid>
    //     );
    // }

    const [openTT, setOpenTT] = useState(false);

    const handleTooltipClose = () => {
        setOpenTT(false);
    };

    const handleTooltipOpen = () => {
        setOpenTT(true);
    };

    return (
        <div>
            <React.Fragment>
                <Grid container alignItems="flex-end">
                    <Grid item xs={11}>
                        <Typography variant="h6" gutterBottom>
                            Liquidity Settings
                        </Typography>
                    </Grid>
                    <Grid item xs={1}>
                        <ClickAwayListener onClickAway={handleTooltipClose}>
                            <HtmlTooltip
                                title={
                                    <React.Fragment>
                                        <Box m={2}>
                                            <div><b>{"Use of liquidity settings"}</b></div>
                                            <div>{"Balance Bot uses the liquidity settings to determine the performance of your balancing portfolio. It compares the current portfolio value with the starting portfolio value to calculate change metrics and performance graphs."}</div>
                                            <div>&nbsp;</div>
                                            <div><b>{"Default liquidity settings"}</b></div>
                                            <div>{"By default, Balance Bot will check the number of coins in your account and their prices upon first-time configuration, and use these to calculate the starting balance."}</div>
                                            <div>&nbsp;</div>
                                            <div><b>{"Register liquidity changes"}</b></div>
                                            <div>{"If you're adding or removing coins after the start date, register those liquidity changes on the respective date, so that your bot can 'normalize' its metrics and performance graphs stay accurate. Do so by selecting the appropriate date, and fill in the number of coins and its price, expressed in your selected base coin, at which you purchased / sold those coins. Simply click on the cells in the table to edit."}</div>
                                            <div>&nbsp;</div>
                                            <div>{"Note: if you've included or excluded a coin in step 2 of the configuration, Balance Bot will automatically suggest to add/remove those coins in the table. This is indicated with a checkmark in the 'suggested' column."}</div>
                                            <div>&nbsp;</div>
                                            <div>{"For dates other than the start date, you can completely remove the liquidity changes for the selected date by clicking the 'delete liq changes' button. This will not affect liquidity changes for other dates, those will be kept."}</div>
                                            <div>&nbsp;</div>
                                            <div>{"For more information about the normalization concept, read "}<Link href="https://hodlerhacks.com/balance-bot/using-balance-bot/liquidity-changes/" target="_blank" rel="noopener" color="secondary">here</Link>{"."}</div>
                                            {/* <div>&nbsp;</div>
                                            <div>{"When clicking the 'fetch balance' button, Balance Bot will retrieve the current balance to fill the table. This may be useful if you want to reset the starting balance."}</div> */}
                                            {/* <div>&nbsp;</div>
                                            <div><b>{"Recreate performance history"}</b></div>
                                            <div>{"Balance Bot can 'recreate history' by analyzing trades that were executed as of the start date, and use that information to create performance graphs for that period. Tick the 'Recreate performance history' checkbox to confirm that you want to do so. Make sure to set the start date right "}<i>{"after"}</i>{" the date when you bought the coins in your portfolio, to avoid these purchases to be counted twice."}</div>
                                            <div>&nbsp;</div>
                                            <div>{"Depending on the start date and your trade history, it may take several minutes to recreate your performance history and until this is visible on the performance screen. You will receive a notification message once the process has completed."}</div>
                                            <div>&nbsp;</div>
                                            <div><i>{"Note: "}</i>{"Recreating the performance history will also recreate your event history, including all executed orders since the start date, but excluding error messages that may have been logged in the past."}</div> */}
                                        </Box>
                                    </React.Fragment>
                                }
                                onClose={handleTooltipClose}
                                open={openTT}
                                disableFocusListener
                                disableHoverListener
                                disableTouchListener
                                interactive
                            >
                                <IconButton onClick={openTT ? handleTooltipClose : handleTooltipOpen}>
                                    <InfoOutlinedIcon color="secondary" />
                                </IconButton>
                            </HtmlTooltip>
                        </ClickAwayListener>
                    </Grid>
                </Grid>
                <div style={{ width: '952px' }}>
                    {/* <CustomToolbar /> */}
                    <Grid container direction="row" alignItems="flex-end" justify="space-between">
                        <Grid item xs={9} >
                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                <DatePicker
                                    open={pickerOpen}
                                    onOpen={() => setPickerOpen(true)}
                                    onClose={() => setPickerOpen(false)}
                                    disableToolbar="true"
                                    variant="inline"
                                    format="yyyy/MM/dd"
                                    margin="normal"
                                    id="date-picker-inline"
                                    label="Select date"
                                    value={selectedDateRef.current}
                                    disableFuture="true"
                                    minDate={parse(input.startdate, 'yyyy/MM/dd', new Date())}
                                    onChange={handleDateChange}
                                    onAccept={acceptDateChange}
                                    renderDay={renderDay}
                                />
                            </MuiPickersUtilsProvider>
                        </Grid>
                        <Grid item xs={3}>
                            <Box display="flex" justifyContent="flex-end">
                                <Button disabled={liqIndexRef.current === 0 || liqIndexRef.current === -1} variant="contained" color="primary" startIcon={<ArrowDownwardIcon />} onClick={handleDelete}>
                                    Delete Liq Changes
                                </Button>
                            </Box>
                        </Grid>
                    </Grid>
                    <Box pt={3}>
                        <Grid container>
                            <Paper className={classes.paper} elevation={0}>
                                <div style={{ width: '952px' }}>
                                    <DataGrid className={classes.header} rows={balanceDay} columns={columns}
                                        autoHeight={true}
                                        density='compact'
                                        disableColumnSelector={true}
                                        disableColumnMenu={true}
                                        hideFooter={true}
                                        hideFooterPagination={true}
                                        hideFooterRowCount={true}
                                        onEditCellChangeCommitted={handleEditCellChangeCommitted}
                                    />
                                </div>
                            </Paper>
                        </Grid>
                    </Box>
                </div>
            </React.Fragment>
        </div>
    );
}